/**
 * import 영역
 */
import 'babel-polyfill';
import Vue from 'vue';
import App from '@/App.vue';
import router from '@/routes/routes';
import store from '@/store/store';
import { formatDate, filterDate } from '@/utils/filters';
import VCalendar from 'v-calendar';
// sweetAlert2
import VueSweetalert2 from 'vue-sweetalert2';
import 'sweetalert2/dist/sweetalert2.min.css';
import PulseLoader from 'vue-spinner/src/PulseLoader.vue';
import RingLoader from 'vue-spinner/src/RingLoader.vue';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import { TimePicker } from 'element-ui';
import lang from 'element-ui/lib/locale/lang/ko';
import locale from 'element-ui/lib/locale';
import vuetify from './plugins/vuetify';
/**
 * import end
 */

/**
 * 옵션 영역
 */
// sweettAlert2 options
const options = {
	confirmButtonColor: '#41b882',
	cancelButtonColor: '#ff7674',
};
// calendarOption
const calendarOption = {
	masks: {
		title: 'MMMM YYYY',
		weekdays: 'W',
		navMonths: 'MMM',
		input: 'YYYY-MM-DD',
		data: 'YYYY-MM-DD',
	},
	datePicker: {
		popover: {
			visibility: 'click',
		},
	},
};
/**
 * 옵션 end
 */

/**
 * use 영역
 */
locale.use(lang);
Vue.component('pulse-loader', PulseLoader);
Vue.component('ring-loader', RingLoader);
Vue.use(ElementUI);
Vue.use(TimePicker);
Vue.use(VueSweetalert2, options);
Vue.filter('formatDate', formatDate);
Vue.filter('filterDate', filterDate);
Vue.use(VCalendar, calendarOption);
/**
 * use end
 */

Vue.mixin({
	methods: {
		// 전역 알림 창
		sAlert(text, title, confirmButtonText) {
			title = title ? title : '알림';

			text = text ? text : '';
			confirmButtonText = confirmButtonText ? confirmButtonText : '확인';

			this.$swal({
				title,
				text,
				confirmButtonText,
				icon: 'info',
			});
		},
		// 전역 경고창
		sWarnning(text, title, confirmButtonText) {
			title = title ? title : '경고';
			text = text ? text : '';
			confirmButtonText = confirmButtonText ? confirmButtonText : '확인';
			this.$swal({
				title,
				text,
				confirmButtonText,
				icon: 'warning',
			});
		},
		// 전역 확인 창
		async sConfirm(text, param) {
			let params = {};
			// 파라미터로 온 값들을 세팅
			for (let data in param) {
				params.append(data, param[data]);
			}
			// 내용 세팅
			if (text) {
				params.title = text;
			}

			// 취소 버튼
			params.showCancelButton = true;
			// 확인 버튼에 텍스트 내용이 없다면 '예'로 초기화
			if (!params.confirmButtonText) {
				params.confirmButtonText = '예';
			}
			if (!params.cancelButtonText) {
				params.cancelButtonText = '아니오';
			}
			return await this.$swal(params).then(result => {
				return result.isConfirmed;
			});
		},
	},
});

Vue.config.productionTip = false;

export const eventBus = new Vue();

new Vue({
	render: h => h(App),
	router,
	vuetify,
	store,
}).$mount('#app');
